<template>
  <div class="Pagination" v-if="pages > 1">
    <div @click="goToBegin">&lt;&lt;</div>
    <div @click="prevPage">Prev</div>
    <ul class="Pagination__list">
      <li class="Pagination__item"
          v-for="page in pages"
          :class="{'Pagination__item--active': page === current}"
          :key="page" @click="goTo(page)">
        {{ page }}
      </li>
    </ul>
    <div @click="nextPage">Next</div>
    <div @click="goToEnd">&gt;&gt;</div>
  </div>
</template>

<script>
export default {
  name: 'Pagination',
  props: {
    pages: {
      type: Number,
      required: true
    },
    currentPage: {
      type: Number,
      required: false
    }
  },
  data () {
    return {
      current: this.current || 1
    }
  },
  watch: {
    current (value) {
      this.current = value
      this.$emit('updatePage', value)
    }
  },
  methods: {
    goTo (page) {
      if (page === this.current) return
      this.current = page
    },
    prevPage () {
      if (this.current > 1) {
        --this.current
      }
    },
    nextPage () {
      if (this.current < this.pages) {
        ++this.current
      }
    },
    goToBegin () {
      this.current = 1
    },
    goToEnd () {
      this.current = this.pages
    }
  }
}
</script>

<style lang="scss">
.Pagination__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline;
}

.Pagination__item {
  display: inline;
}

.Pagination__item--active {
  color: red;
}
</style>
